ul.todo-list li {
    display: flex;
    border-top: 1px solid #dfe6e9;
    border-bottom: 1px solid #dfe6e9;
  }

  .remove {
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    width: 50px;
    color: #ff7675;

  }

  .todo {
    flex: 1;
    outline: none;
    cursor: pointer;
    font-size: 1.5rem;
    word-break: break-all;
    line-height: 1.2;
    transition: color 0.3s;
    padding: 15px 15px 17px 15px;
  }

  .todo.checked {
    color: #b2cec3;
    text-decoration: line-through;
  }
